.catalog-wrap {
  position: relative;
  background-color: #edeff6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 76px;
  margin-top: -30px;
  padding-top: 30px;
  z-index: 1;

  @media(min-width: $tablet) {
    padding-bottom: 111px;
  }

  @media (min-width: $desktop) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    margin-top: -60px;
    padding-top: 60px;
    padding-bottom: 205px;
  }
}
